/**
 * ID
 * @param id IDURL
 * @returns ID
 */
export const cleanRoomId = (id: string): string => {
  // 
  if (!id) return '';

  // 
  let cleanedId = id.trim();

  // URLroomId
  if (cleanedId.startsWith('http')) {
    try {
      const url = new URL(cleanedId);
      const roomIdParam = url.searchParams.get('roomId');
      if (roomIdParam) {
        console.log(`URLroomId: ${cleanedId} -> ${roomIdParam}`);
        cleanedId = roomIdParam.trim();
      } else {
        // URLroomId
        const pathParts = url.pathname.split('/').filter(Boolean);
        if (pathParts.length > 0) {
          const lastPathPart = pathParts[pathParts.length - 1];
          if (lastPathPart && lastPathPart.length > 5) {
            console.log(`URLroomId: ${cleanedId} -> ${lastPathPart}`);
            cleanedId = lastPathPart.trim();
          }
        }
      }
    } catch (error) {
      console.error('URL:', error);
      // URL
    }
  }

  //  ID  (hwW6wz-hwW6wz)
  if (cleanedId.includes('-')) {
    const parts = cleanedId.split('-');
    // 
    if (parts[0] === parts[1]) {
      console.log(`ID: ${cleanedId} -> ${parts[0]}`);
      cleanedId = parts[0];
    } else {
      //  PeerJS 
      console.log(`ID: ${cleanedId} -> ${parts[0]}`);
      cleanedId = parts[0];
    }
  }

  // 
  cleanedId = cleanedId.replace(/[^\w\-]/g, '');

  // IDPeerJS ID16
  // ID16
  if (cleanedId.length > 20) {
    console.log(`ID16: ${cleanedId} -> ${cleanedId.substring(0, 16)}`);
    cleanedId = cleanedId.substring(0, 16);
  }

  return cleanedId;
};
